<template>
    <div class="sigin_title">
        <h1></h1>
        <h6>  </h6>
    </div>
    <div class="container">
        <div class="row align-items-center">
            <div class="col-12 col-sm-8 col-md-6 col-lg-4 offset-0 offset-sm-2 offset-md-3 offset-lg-4">

                <Form @submit="this.signUpWithEmailAndPassword" :validation-schema="schema" v-slot="{ errors }">                    
                        <hr />
                        <div class="mb-1">
                            <label class="form-label" for="name"> </label>
                            <Field name="username" type="text" class="form-control" :class="{ 'is-invalid': errors.username }" />
                            <div class="invalid-feedback">{{errors.username}}</div>                    
                        </div>                        
                        <div class="mb-2">
                            <label class="form-label" for="email"></label>
                            <Field name="email" type="email" class="form-control" :class="{ 'is-invalid': errors.email }" />
                            <div class="invalid-feedback">{{errors.email}}</div>
                        </div>
                        <div class="mb-2">
                            <label class="form-label" for="password"></label>
                            <Field name="password" type="password" class="form-control" :class="{ 'is-invalid': errors.password }" />
                            <div class="invalid-feedback">{{errors.password}}</div>                    
                        </div>
                        <div class="mb-2">
                            <label class="form-label" for="password">  </label>
                            <Field name="confirmPassword" type="password" class="form-control" :class="{ 'is-invalid': errors.confirmPassword }" />
                            <div class="invalid-feedback">{{errors.confirmPassword}}</div>                    
                        </div>                        
                        <div class="mb-2">
                            <label class="form-label" for="name"></label>
                            <Field name="telnum" type="text" class="form-control" :class="{ 'is-invalid': errors.telnum }" />
                            <div class="invalid-feedback">{{errors.telnum}}</div>                    
                        </div>
                        <div class="d-flex justify-content-center align-items-center">
                            <b-row>
                                <b-col cols="1"></b-col>
                                <b-col cols="5"><button type="submit" class="btn btn-primary"> </button></b-col>
                                <b-col cols="5"><button type="button" class="btn btn-outline-danger" @click="onCancelClick()"></button></b-col>                            
                                <b-col cols="1"></b-col>
                            </b-row>
                        </div>
                </Form>                
            </div>
        </div>
    </div>
</template>

<script>
import { Form, Field} from 'vee-validate';
import * as Yup from 'yup';
import router from "@/router";

const schema = Yup.object().shape({
    username: Yup.string()
        .required(' .'),
    email: Yup.string()
        .required(' ')
        .email('  '),
    password: Yup.string()
        .min(6, ' 6  ')
        .required(' '),
    confirmPassword: Yup.string()
        .oneOf([Yup.ref('password'), null], '  ')
        .required('  '),
    telnum: Yup.string()
        .min(4, '  4 ')
        .required(' '),
});

export default {
    name: "SignUpView",
    components:{
        Form,
        Field,

    },
    data() {
        return {
        email: '',
        password: '',
        username:'',
        isPasswordVisible: false,
        schema:schema,
        telnum: '',
        };
    },
    methods: {
        async signUpWithEmailAndPassword(value) {
            
            const formData = {
                email:value.email,
                password:value.password,
                username:value.username,
                telnum:value.telnum,
            };

            try {
                await this.$store.dispatch("signUpWithEmailAndPassword", formData);
            } catch (error) {
                this.message = error.message;
            }
        },

        onCancelClick() {
            router.push('/');
        }
    },
};

</script>

<style scoped>
.sigin_title {
    padding: 20px;
    text-align: center;
}
</style>